import React, { useState } from 'react';
    import { motion, AnimatePresence } from 'framer-motion';
    import { Phone, Video, MessageSquare, Clock, Calendar, CalendarDays, CalendarRange } from 'lucide-react';
    import { useNavigate } from 'react-router-dom';

    const DecisionTunnel = () => {
      const [currentStep, setCurrentStep] = useState(0);
      const [selectedSituation, setSelectedSituation] = useState('');
      const [selectedTime, setSelectedTime] = useState('');
      const [selectedMethod, setSelectedMethod] = useState('');
      const [contact, setContact] = useState('');
      const [clientName, setClientName] = useState('');
      const [clientDob, setClientDob] = useState('');

      const navigate = useNavigate();

      const situations = [
        { id: 'arrested', label: 'Arrested / Just released', icon: 'ðŸš”' },
        { id: 'charged', label: 'Charged but not arrested', icon: 'ðŸ“„' },
        { id: 'court', label: 'Court date coming up', icon: 'âš–ï¸' },
        { id: 'family', label: 'Someone I love needs help', icon: 'ðŸ‘¤' },
        { id: 'unsure', label: 'Not sure yet', icon: 'â“' }
      ];

      const timeOptions = [
        { id: 'now', label: 'Now', icon: Clock },
        { id: 'today', label: 'Today', icon: Calendar },
        { id: 'tomorrow', label: 'Tomorrow', icon: CalendarDays },
        { id: 'week', label: 'This week', icon: CalendarRange }
      ];

      const contactMethods = [
        { id: 'phone', label: 'Phone', icon: Phone },
        { id: 'video', label: 'Video', icon: Video },
        { id: 'text', label: 'Secure text', icon: MessageSquare }
      ];

      const handleSituationSelect = (situation: string) => {
        setSelectedSituation(situation);
        setTimeout(() => setCurrentStep(2), 250);
      };

      const handleTimeSelect = (time: string) => {
        setSelectedTime(time);
        setTimeout(() => setCurrentStep(3), 250);
      };

      const handleMethodSelect = (method: string) => {
        setSelectedMethod(method);
      };

      const handleContactSubmit = (e: React.FormEvent) => {
        e.preventDefault();
        // Proceed to collect minimal client info
        setCurrentStep(4);
      };

      const handleFinalSubmit = (e: React.FormEvent) => {
        e.preventDefault();

        const createdAt = new Date().toISOString();
        // Minimal "user" object stored locally to simulate logged-in session
        const user = {
          name: clientName || 'Client',
          contact,
          contactMethod: selectedMethod,
          situation: selectedSituation,
          preferredTime: selectedTime,
          dob: clientDob || null,
          createdAt,
          cases: [
            {
              id: `case_${Date.now()}`,
              title: selectedSituation ? selectedSituation.replace(/_/g, ' ') : 'New case',
              status: 'Intake scheduled',
              createdAt,
            },
          ],
        };

        try {
          localStorage.setItem('defense_user', JSON.stringify(user));
        } catch (err) {
          // ignore storage errors
          console.error('Failed saving user', err);
        }

        // Navigate to dashboard
        navigate('/dashboard');
      };

      const stepVariants = {
        hidden: { opacity: 0, y: 20 },
        visible: { opacity: 1, y: 0 },
        exit: { opacity: 0, y: -20 }
      };

      return (
        <div className="min-h-screen bg-black flex items-center justify-center px-4">
          <div className="w-full max-w-2xl">
            <AnimatePresence mode="wait">
              {currentStep === 0 && (
                <motion.div
                  key="step0"
                  variants={stepVariants}
                  initial="hidden"
                  animate="visible"
                  exit="exit"
                  transition={{ duration: 0.5 }}
                  className="text-center"
                >
                  <motion.h1
                    className="text-4xl md:text-6xl font-light text-white mb-12 leading-tight"
                    animate={{ opacity: [0.8, 1, 0.8] }}
                    transition={{ duration: 3, repeat: Infinity }}
                  >
                    You don't have to handle this alone.
                  </motion.h1>

                  <motion.button
                    onClick={() => setCurrentStep(1)}
                    className="bg-red-600 hover:bg-red-700 text-white px-12 py-6 text-xl font-medium rounded-lg transition-all duration-300 transform hover:scale-105 focus:outline-none focus:ring-4 focus:ring-red-500/50"
                    whileHover={{ scale: 1.05 }}
                    whileTap={{ scale: 0.95 }}
                  >
                    I need a criminal lawyer now
                  </motion.button>
                </motion.div>
              )}

              {currentStep === 1 && (
                <motion.div
                  key="step1"
                  variants={stepVariants}
                  initial="hidden"
                  animate="visible"
                  exit="exit"
                  transition={{ duration: 0.5 }}
                  className="text-center"
                >
                  <h2 className="text-3xl md:text-4xl font-light text-white mb-12">
                    What are you dealing with right now?
                  </h2>

                  <div className="grid grid-cols-1 md:grid-cols-2 gap-4 max-w-3xl mx-auto">
                    {situations.map((situation) => (
                      <motion.button
                        key={situation.id}
                        onClick={() => handleSituationSelect(situation.id)}
                        className="bg-white/5 hover:bg-white/10 border border-white/20 text-white p-6 rounded-lg text-left transition-all duration-300 focus:outline-none focus:ring-2 focus:ring-white/30"
                        whileHover={{ scale: 1.02 }}
                        whileTap={{ scale: 0.98 }}
                      >
                        <div className="text-2xl mb-2">{situation.icon}</div>
                        <div className="text-lg">{situation.label}</div>
                      </motion.button>
                    ))}
                  </div>
                </motion.div>
              )}

              {currentStep === 2 && (
                <motion.div
                  key="step2"
                  variants={stepVariants}
                  initial="hidden"
                  animate="visible"
                  exit="exit"
                  transition={{ duration: 0.5 }}
                  className="text-center"
                >
                  <div className="mb-8">
                    <h2 className="text-3xl md:text-4xl font-light text-white mb-4">
                      This is fixable.
                    </h2>
                    <p className="text-xl text-white/80 mb-8">
                      Timing matters.
                    </p>
                    <p className="text-lg text-white/60">
                      The next step is a private call with a defense attorney.
                    </p>
                  </div>

                  <h3 className="text-2xl text-white mb-8">When works best?</h3>

                  <div className="grid grid-cols-2 md:grid-cols-4 gap-4 max-w-2xl mx-auto">
                    {timeOptions.map((time) => {
                      const IconComponent = time.icon;
                      return (
                        <motion.button
                          key={time.id}
                          onClick={() => handleTimeSelect(time.id)}
                          className="bg-white/5 hover:bg-white/10 border border-white/20 text-white p-6 rounded-lg transition-all duration-300 focus:outline-none focus:ring-2 focus:ring-white/30"
                          whileHover={{ scale: 1.02 }}
                          whileTap={{ scale: 0.98 }}
                        >
                          <IconComponent className="w-8 h-8 mx-auto mb-2" />
                          <div className="text-sm">{time.label}</div>
                        </motion.button>
                      );
                    })}
                  </div>
                </motion.div>
              )}

              {currentStep === 3 && (
                <motion.div
                  key="step3"
                  variants={stepVariants}
                  initial="hidden"
                  animate="visible"
                  exit="exit"
                  transition={{ duration: 0.5 }}
                  className="text-center"
                >
                  <h2 className="text-3xl md:text-4xl font-light text-white mb-12">
                    How should we connect?
                  </h2>

                  <div className="grid grid-cols-1 md:grid-cols-3 gap-4 max-w-2xl mx-auto mb-12">
                    {contactMethods.map((method) => {
                      const IconComponent = method.icon;
                      return (
                        <motion.button
                          key={method.id}
                          onClick={() => handleMethodSelect(method.id)}
                          className={`border text-white p-6 rounded-lg transition-all duration-300 focus:outline-none focus:ring-2 focus:ring-white/30 ${
                            selectedMethod === method.id
                              ? 'bg-white/20 border-white/40'
                              : 'bg-white/5 hover:bg-white/10 border-white/20'
                          }`}
                          whileHover={{ scale: 1.02 }}
                          whileTap={{ scale: 0.98 }}
                        >
                          <IconComponent className="w-8 h-8 mx-auto mb-2" />
                          <div className="text-lg">{method.label}</div>
                        </motion.button>
                      );
                    })}
                  </div>

                  {selectedMethod && (
                    <motion.form
                      onSubmit={handleContactSubmit}
                      initial={{ opacity: 0, y: 20 }}
                      animate={{ opacity: 1, y: 0 }}
                      className="max-w-md mx-auto"
                    >
                      <label className="block text-white text-lg mb-4">
                        Where should we reach you?
                      </label>
                      <input
                        type={selectedMethod === 'phone' ? 'tel' : 'email'}
                        value={contact}
                        onChange={(e) => setContact(e.target.value)}
                        placeholder={selectedMethod === 'phone' ? 'Your phone number' : 'Your email address'}
                        className="w-full bg-white/10 border border-white/20 text-white placeholder-white/50 px-4 py-3 rounded-lg text-lg focus:outline-none focus:ring-2 focus:ring-white/30 focus:border-white/40"
                        required
                      />
                      <motion.button
                        type="submit"
                        className="w-full bg-red-600 hover:bg-red-700 text-white px-8 py-4 text-lg font-medium rounded-lg mt-6 transition-all duration-300 focus:outline-none focus:ring-4 focus:ring-red-500/50"
                        whileHover={{ scale: 1.02 }}
                        whileTap={{ scale: 0.98 }}
                      >
                        Continue
                      </motion.button>
                    </motion.form>
                  )}
                </motion.div>
              )}

              {currentStep === 4 && (
                <motion.div
                  key="step4"
                  variants={stepVariants}
                  initial="hidden"
                  animate="visible"
                  exit="exit"
                  transition={{ duration: 0.5 }}
                  className="text-center"
                >
                  <h2 className="text-3xl md:text-4xl font-light text-white mb-6">
                    Quick info so we can identify you
                  </h2>
                  <p className="text-lg text-white/60 mb-8">
                    We only need the essentials. Attorney will gather details on the call.
                  </p>

                  <motion.form
                    onSubmit={handleFinalSubmit}
                    initial={{ opacity: 0, y: 20 }}
                    animate={{ opacity: 1, y: 0 }}
                    className="max-w-md mx-auto text-left"
                  >
                    <label className="block text-white text-sm mb-2">First name</label>
                    <input
                      value={clientName}
                      onChange={(e) => setClientName(e.target.value)}
                      placeholder="First name"
                      className="w-full bg-white/10 border border-white/20 text-white placeholder-white/50 px-4 py-3 rounded-lg text-lg focus:outline-none focus:ring-2 focus:ring-white/30 focus:border-white/40 mb-4"
                      required
                    />

                    <label className="block text-white text-sm mb-2">Date of birth (optional)</label>
                    <input
                      value={clientDob}
                      onChange={(e) => setClientDob(e.target.value)}
                      placeholder="YYYY-MM-DD"
                      className="w-full bg-white/10 border border-white/20 text-white placeholder-white/50 px-4 py-3 rounded-lg text-lg focus:outline-none focus:ring-2 focus:ring-white/30 focus:border-white/40 mb-6"
                    />

                    <motion.button
                      type="submit"
                      className="w-full bg-red-600 hover:bg-red-700 text-white px-8 py-4 text-lg font-medium rounded-lg transition-all duration-300 focus:outline-none focus:ring-4 focus:ring-red-500/50"
                      whileHover={{ scale: 1.02 }}
                      whileTap={{ scale: 0.98 }}
                    >
                      Schedule confidential call
                    </motion.button>
                  </motion.form>
                </motion.div>
              )}

            </AnimatePresence>
          </div>
        </div>
      );
    };

    export default DecisionTunnel;